import "./index.scss";
import Msgtoggle from "../../mobx";
import {observer} from "mobx-react";

export const foots = [
    {txt:"探索",path:"/index/tansuo",name:"tansuo",icon:"icon-search"},
    {txt:"心愿单",path:"/index/xinyuandan",name:"xinyuandan",icon:"icon-heart2e"},
    {txt:"故事",path:"/index/gushi",name:"gushi",icon:"icon-Leaf-"},
    {txt:"收件箱",path:"/index/shoujianxiang",name:"shoujianxiang",icon:"icon-message"},
    {txt:"我的",path:"/index/wode",name:"wode",icon:"icon-user11"}
]

import {NavLink} from "react-router-dom";


@observer
export default class Foot extends Component{
    constructor(){
        super();
        this.state = {
            foots,
            query:"",
            isLogin:false
        }
    }
    componentDidMount(){
        if(sessionStorage.userPhone){
            this.setState({
                isLogin: true
            })
            if(sessionStorage.checked == 0){
                Msgtoggle.msgRead();
            }else if(sessionStorage.checked > 0){
                Msgtoggle.msgSend();
            }
        }
        if(location.hash.slice(1,8)=="/houses"){
            const active = document.getElementsByTagName("footer")[0].getElementsByTagName("a")[0];
            active.setAttribute("class","nav-active");
        }
    }
    render(){
        const {
            foots,
            isLogin
        } = this.state;
        
        return (
            <footer>
                {
                    foots.map((foot,i)=>{
                        return (
                            <div key={i}>
                                <NavLink to={foot.path} activeClassName="nav-active">
                                    <i className={"iconfont "+foot.icon}></i>
                                    <span>{foot.txt}</span>
                                    {isLogin&&Msgtoggle.msgtoggle&&i==3&&
                                        <div style={{backgroundColor:"#ff5a5f"}} className="hint"></div>
                                    }
                                </NavLink>
                            </div>
                        )
                    })
                }
            </footer>
        )
    }
}